import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/pages/home'
import productDetail from '@/components/pages/productDetail'
import standard from '@/components/pages/standard'
import modifyEnroll from '@/components/pages/modifyEnroll'
import accountdata from '@/components/accountdata'
import changePassword from '@/components/changepassword'
import loading from '@/components/pages/loading'
import serviceterm from '@/components/pages/serviceterm'
import privacyterm from '@/components/pages/privacyterm'
import aboutYFP from '@/components/pages/aboutYFP'
import contactus from '@/components/contactus'
import workslayout from '@/components/workslayout'
import serviceContent from '@/components/pages/serviceContent'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/productDetail/:id',
      name: 'productDetail',
      component: productDetail
    },
    {
      path: '/standard/:id',
      name: 'standard',
      component: standard
    },
    {
      path: '/serviceterm',
      name: 'serviceterm',
      component: serviceterm
    },
    {
      path: '/privacyterm',
      name: 'privacyterm',
      component: privacyterm
    },
    {
      path: '/aboutYFP',
      name: 'aboutYFP',
      component: aboutYFP
    },
    {
      path: '/serviceContent',
      name: 'serviceContent',
      component: serviceContent,
      children: [
        {
          path: 'contactus',
          name: 'contactus',
          component: contactus
        },
        {
          path: 'workslayout',
          name: 'workslayout',
          component: workslayout
        }
      ]
    },
    {
      path: '/modifyEnroll',
      name: '',
      component: modifyEnroll,
      meta: { requiresAuth: true },
      children: [{
        path: '',
        name: 'products',
        component: accountdata,
        meta: { requiresAuth: true }
      }, {
        path: 'changePassword',
        name: 'changePassword',
        component: changePassword
      }]
    },
    {
      path: '/loading',
      name: loading,
      component: loading
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



